import React from "react";

const  Header  = (props) => {

    const {search,onInputChange, onSearchClick} = props;
  
  return (
  <div className ="jumbotron">
      <h1 className="Display-1"><span class="material-icons bicon">
fastfood
</span>  Food Recipes</h1>
<div class="input-group w-50 mx-auto">
  <input type="text" class="form-control" placeholder="Search your Recipe...."
   value={search}
    onChange={onInputChange}
   />

  <div class="input-group-append">
    <button className="btn btn-dark" onClick={onSearchClick}>Search Recipes</button>
  </div>
</div>
      </div>    
  );
};

export default Header;
